<template>
  <div>
    <h2>Your Cart</h2>
    <div v-if="cartItems.length">
      <ul>
        <li v-for="item in cartItems" :key="item.id">
          {{ item.title }} x {{ item.quantity }}
        </li>
      </ul>
      <p>Total: ${{ cartTotalPrice.toFixed(2) }}</p>
    </div>

    <p v-else><em>Please add some products to cart.</em></p>
    <button @click="checkOutCart" :disabled="isCheckDisable">Check Out</button>

    <p v-show="checkOutState">CheckOut {{ checkOutState }}</p>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";
const { mapGetters, mapState, mapActions } = createNamespacedHelpers("cart");
export default {
  computed: {
    ...mapState(["checkOutState"]),
    ...mapGetters(["cartItems", "cartTotalPrice"]),
    ...mapState({
      isCheckDisable(state) {
        return state.selects.length == 0;
      },
    }),
  },

  methods: {
    ...mapActions(["checkOutCart"]),
  },
};
</script>