<template>
  <div>
    <li>{{ item.title }} - ${{ item.price }}</li>
    <button @click="$emit('addToCart', item)" :disabled="isBtnDisable">
      Add to cart
    </button>
  </div>
</template>

<script>
export default {
  props: ["item"],
  computed: {
    isBtnDisable() {
      return this.item.inventory <= 0;
    },
  },
};
</script>

<style scoped>
div {
  margin-bottom: 20px;
}
</style>