import shop from "../../api/shop";

const state = {
    selects: [], // 已选中列表 {id: x, quantity: 1}
    checkOutState: null, // 当前check out状态
}

const getters = {
    cartItems(state, getters, rootState) {
        return state.selects.map(({
            id,
            quantity
        }) => {
            const product = rootState.products.products.find(product => product.id === id);
            return {
                id: product.id,
                title: product.title,
                price: product.price,
                quantity
            }
        })
    },
    cartTotalPrice: (state, getters) => {
        return getters.cartItems.reduce((total, product) => {
            return total + product.price * product.quantity
        }, 0)
    }
}

const mutations = {
    addSelect(state, {
        id
    }) {
        const select = state.selects.find(item => item.id === id);
        if (select) {
            select.quantity++;
            return;
        }

        state.selects.push({
            id,
            quantity: 1
        });
    },
    setCheckOutState(state, payload) {
        state.checkOutState = payload;
    },
    clearCart(state) {
        state.selects = [];
    },
    backCart(state, carts) {
        state.selects = carts;
    }
}

const actions = {
    // 添加一个商品到购物车
    addProductToCart({
        commit
    }, product) {
        commit('setCheckOutState', null);
        if (product.inventory > 0) {
            console.log("添加购物车");
            commit('addSelect', {
                id: product.id
            });
            commit('products/decrementProduct', {
                id: product.id
            }, {
                root: true
            });
        }
    },
    checkOutCart({
        state,
        commit
    }) {
        const backUpSelects = state.selects; // 备份数据
        commit('clearCart');
        console.log(state.checkOutState, state.selects.length);
        shop.buyProducts(backUpSelects, () => {
            commit('setCheckOutState', 'successful');
        }, () => {
            commit('backCart', backUpSelects);
            commit('setCheckOutState', 'fail');
        })
    }
}

// id title count

export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
}