<template>
  <div>
    <h2>Products</h2>
    <ul>
      <ProductsItem
        v-for="item in items"
        :key="item.id"
        :item="item"
        @addToCart="addToCart"
      ></ProductsItem>
    </ul>
  </div>
</template>

<script>
import ProductsItem from "./ProductsItem.vue";
import { createNamespacedHelpers } from "vuex";

const { mapState } = createNamespacedHelpers("products");
export default {
  components: {
    ProductsItem,
  },
  created() {
    this.$store.dispatch("products/getProducts");
  },
  methods: {
    addToCart(item) {
      this.$store.dispatch("cart/addProductToCart", item);
    },
  },

  computed: {
    ...mapState({
      items: "products",
    }),
  },
};
</script>

<style scoped>
ul {
  /* list-style-type: none; */
  padding-left: 20px;
}
</style>